/*=====================
Dark start
==========================*/
body {
  &.dark {
    background-color: $dark-layout !important;
    transition: all 0.3s ease;
    color: $font-layout-5;
  }
}
.dark {
  .product-notification{
    background-color: $light-layout;
    .close{
      color: $light-border;
    }
  }
  h1{
    color:$white;
  }
  h2 {
    color:$font-layout-1 ;
  }
  h3 {
    color:$font-layout-2 ;
  }
  h4 {
    color:$font-layout-3 ;
  }
  h5 {
    color:$font-layout-4 ;
  }
  h6 {
    color:$font-layout-5 ;
  }

  p {
    color: $font-layout-5 ;
  }
  li {
    color: $font-layout-5 ;
  }
  a {
    color: $dark-link;
  }
  input,textarea {
    border-color: $dark-border !important;
    background-color: $light-layout;
    &::placeholder {
      color: $font-layout-5 !important;
    }
  }
  select {
    background-color: $dark-layout !important;
    color: $font-layout-5 !important;
    &:focus {
      background-color: $light-layout;
      color: $font-layout-5 !important;
    }
  } 
  .gift-dropdown{
    &.dropdown-menu {
      border: 2px solid $dark-border;
      background-color: $dark-layout;
      .media {
        &+ .media {
          border-top: 1px solid $dark-border;
        }
      }
    }


  } 
  .header-category {
    ul{
      &.collapse-category{
        background-color:$light-layout;
        border-color: $dark-border;
        li {
          &.back-btn{
            border-color: $dark-border;
          }
          a{
            border-color: $dark-border;
            color: $white-1;
          }
          &:first-child{
            color: $white-1;
          }
        }
      }      
    }
  }
  .header-category2 {
    ul{
      &.collapse-category{
        background-color:$light-layout;
        border-color: $dark-border;
        li{
          a{
            color: $white;
          }
          &:nth-child(n+3) {
            a{
              border-color: $dark-border;
              
            }
          }
        }
      }
    }
  }
  .header-search {
    .input-group {
      .form-control{
        color: $white-1;
        &:focus{
          background-color:$light-layout;
        }
      }
    }

    &.search-style2 {
      .input-group{
        border-color: $dark-border;
      }
    }
  }


  
  .header-search2 {
    .input-group {
       .input-group-append {
        .input-group-text{
          border: none;
        }
       }
      .form-control{
        color: $white-1;
        &:focus{
          background-color:$light-layout;
        }
      }
    }
  }
  .header-search3 {
    .input-group {
      .form-control{
        color: $white-1;
        &:focus{
          background-color:$light-layout;
        }
      }
    }
  }
  label {
    color: $font-layout-1 !important;
  }
  .toggle-nav i{
    color: $font-layout-1;
  }  
  .sm-horizontal,.sm-vertical {
    .mobile-back {
      color: $font-layout-3;
      border-bottom-color: $dark-border;
    }
  }
  .tab-product-main .tab-prodcut-contain ul li a{
    color: $white;
  }
  .tab-product-main{
    &.tab-second {
      .tab-prodcut-contain {
        ul {
          li {
            a {
              svg{
                fill:$white;
              }
            }
          }
        }
      }
    }
  } 
  .blog {
    .blog-contain{
      .blog-img{
        border: 1px solid $light-layout;
      }
      .blog-details-2{
        background-color: $light-layout;
      }
    }
  }
  .theme-pannel-main{
    background-color: $light-layout;
    ul {
      li{
        border-color: $dark-border;
        span{
          color: $white-1;
        }
      }
    }
  }
  .color-picker {
    background-color:$light-layout;
    h4{
      border-color: $dark-border;
    }
    .title{
      background-color: $light-layout;
    }
  }


  // header //
  .input-block {
    .input-box {
      .big-deal-form {
        border-color: $dark-border;
        .input-group{
          background-color:$light-layout;
          span{
            background-color:$light-layout;          
          }
          .form-control{
            &:focus{
              background-color:$light-layout;
            }
          }
          select{
            background-color:$light-layout;
          }
        }
      }
    }
  } 
  .searchbar-input {
    .input-group {
      .input-group-append{       
          .input-group-text{
            background-color:$light-layout;
          }       
      }
    }
  }
  .sm-nav-block {
    .nav-slide {
      background-color:$light-layout;
      > li {
        border-color: $dark-border;
        .nav-sm-back{
            color: $font-layout-2;
            border-color: $dark-border;
        }
        a{
          color: $font-layout-5;
        }
        &.mor-slide-open {
          ul {
            li{
              border-color: $dark-border;
            }
          }
        }
      }
    }
  }
  .layout-header2{
    background-color:$light-layout;
    .main-menu-block {
      .icon-block {
        ul {
          li {
            svg{
              fill: $dark-link;
            }
            .cart-block {
              .cart-item {
                h5{
                  color: $dark-link;
                }
              }
            }
          }
        }
      }
    }
  }
  .category-header-2 {
    .navbar-menu {
      .nav-block {
        .nav-left {
          .nav-cat{
            background-color:$light-layout;           
            li {
              img{
                border-color: $dark-border;
              }
              a{
                color: $dark-link;
              }
            }
          }
        }
      }
    }
  }
  .layout-header1{
    background-color:$dark-layout;
    .main-menu-block {
      .menu-right {
        .icon-nav {
          ul {
            li {
              svg{
                fill: $dark-link;
              }
              .cart-item {
                span{
                  color: $dark-link;
                }
              }
            }
          }
        }
      }
      .menu-left {
        .sm-nav-block {
          .nav-slide{
            background-color:$light-layout;
            > li {
              border-color: $dark-border;
              a{
                color: $dark-link;
              }
              .nav-sm-back{
                color: $font-layout-5;
                border-color: $dark-border;
              }
              &.mor-slide-open {
                ul {
                  li{
                    border-color: $dark-border;
                  }
                }
              }
            }
          }
          
        }
      }
    }
  }
  .category-header {
    .navbar-menu {
      .category-left {
        .nav-block {
          .nav-left {
            .nav-cat{
              background-color:$light-layout;                          
              li{
                img{
                  border-color: $dark-border;
                }
                a{
                  color: $dark-link;
                }
              }
            }
          }
        }
      }
    }
  }
  .layout-header3 {
    .main-menu {
      .menu-left{
        .nav-block {
          .nav-left {
            .nav-cat{
              background-color: $light-layout;
              li{
                a{
                  color: $font-layout-5;
                }
                img{
                  border-color: $dark-border;
                }
              }
            }
          }
        }
        .sm-nav-block {
          .nav-slide{
            background-color:$light-layout1;
            li{
              border-color: $dark-border;
            }
             > li {
              .nav-sm-back{
                color: $font-layout-5;
                border-color: $dark-border;
              }
              border-color: $dark-border;
              a{
                color:$dark-link;
              }
             }             
          }
        }
      }
      .input-block {
        .input-box {
          .big-deal-form{
            border-color: $dark-border;
            .input-group{
              select{
                border-color: $dark-border;
              }
            }
          }
        }
      }
      .menu-nav {
        .desc-horizontal{
          background-color:$light-layout;
          border-color: $dark-border;
          .desc-back{
            color: $font-layout-5;
            border-color: $dark-border;
          }
        }       
        
      }

    }
  }
  .layout-header4{
    background-color:$light-layout;
    .contact-block {
      .desc-nav-block {
        ul {
          li {
            .contact-item{
              color: $dark-link;
              span{
                color: $dark-link;
              }
            }

            
          }
        }
      }
      .sm-nav-block {
        .nav-slide{
          background-color:$light-layout;
          li{
            border-color: $dark-border;
            a{
              color: $dark-link;
            }
            .nav-sm-back{
              color: $font-layout-5;
              border-color: $dark-border;
            }
          }
        }

        
      }
    }
    .icon-block {
      ul {
        li {
          a {
            svg{
              fill: $dark-link;
            }
            .cart-item{
              color:$dark-link;
            }
          }
        }
      }
    }
   
    
  }
  .category-header-4 {
    .navbar-menu {
      .category-left{
        .nav-block {
          .nav-left {
            .nav-cat{
              background-color:$light-layout;
              li {
                a{
                  color: $font-layout-5;
                }
              }
            }
          }
        }
      }
    }
  }
  .header7{
    background-color:$light-layout;
    .header-contain {
      .icon-block {
        ul {
          li {
            svg{
              fill: $dark-link;
            }
          }
        }
      }      
    }
  }

  .category-header7 {
    .category-contain {
      .category-right{
        .menu-block {
          ul {
            li {
              a{
                color: $white !important;
              }
            }
          }
        }
      }
    }
  }
  

  


  
  
  


  .sticky {
    .category-header-2{
      background-color:$light-layout;
      .navbar-menu {
        .icon-block {
          ul {
            li {
              svg{
                fill: $dark-link;
              }
            }
          }
        }
      }
    }

    .category-header-4{
      background-color:$light-layout;
      .navbar-menu {
        .category-right {
          .icon-block {
            ul {
              li {
                svg{
                  fill:$dark-link;
                }
              }
            }
          }
        }
      }
    }
    &.header-style2 {
      .category-header7{
        background-color: $light-layout;
        .category-contain {
          .icon-block {
            ul {
              li {
                svg{
                  fill: $dark-link;
                }
              }
            }
          }
        }
      }
    }
  }


  // ganaral //
  .b-g-white {
    background-color: $light-layout !important;
  }
  .b-g-white1 {
    background-color:$light-layout !important;
  }
  .slick-prev,.slick-next {
    &:before {
      color: $font-layout-2 !important;
    }
  } 
  .bg-light {
    background-color: $dark-layout !important;
  }
  .bg-white {
    background-color: $dark-layout !important;
  }

  .b-g-light{
   background-color: $dark-layout !important; 
  }

  

  // creative card //
  .creative-card {
    background-color: $light-layout;
  }


  // button //
  .btn-rounded{
    &.btn-outline{
      background-color:$light-layout;
      color: $theme-color2 !important;
      &:hover{
        color: $white !important;
      }
    }
  }

  // header // 
  .header-category2 {
    ul{
      &.collapse-category {
        li{
          &.back-btn{
            color: $font-layout-1;
          }
        }
      }
    }
  }
  .header-category3{
    ul{
      &.collapse-category{
        background-color:$light-layout;
        border-color: $dark-border;
        li{
          background-color:$light-layout;         
          a{
            color: $font-layout-2;
          }     
          
          
        }

        > li {
          ul{
            border-color: $dark-border;
          }
        }


      }
      .categoryone {
        .collapse-mega{
          background-color:$light-layout;
          border-color:$dark-border;
          .mega-box {
            h5{
              color:$dark-link;
            }
          }
        }
      }

      li {
        a {
          span{
            &:before{
              color: $dark-link;
            }
          }
        }
      }
    }


  }


  
  



   
  
  // home sldier //
  .pets-slide {
    .slide-main {
      .slide-contain {
        .form-control{
          background-color: $white;
          border-color: $light-border !important;
        }
      }
    }
  }

  // menu //
  .pixelstrap {
    ul {
      border-color: #4c4c4c;
      background-color: $light-layout;
      a,
      &:hover,
      &:active {
        color: $dark-link !important;
      }
    }
    a {
      color: $dark-link !important;
    }
    .link-section {
      h5 {
        color: $font-layout-4;
      }
    }
  }

  // brand panner //
  .brand-panel {
    .brand-panel-box {
      .brand-panel-contain {
        background-color: $light-layout;
        ul {
          li {
            a {
              color: $font-layout-5;
            }
          }
        }
      }
    }
  }
  .brand-second {
    .brand-box{
      background-color: $light-layout;
      img{
        filter: invert(1);
      }
    }
  }

  // team //
  .team1 {
    .team-box{
      background-color: $light-layout;
      .team-detail {
        h5{
          color: $white-1
        }
        p{
          color: $white-2;
        }
      }
    }
  }

  // pricing table//
  .pricing-table1 {
    .pricing-box {
      border-color: $dark-border;
      .pricing-body{
        background-color:$light-layout;
         ul {
           li{
            border-color: $dark-border;
            a{
              color: $white-2;
            }
           }
         }
      }
    }
  }
  .pricing-table2 {
    .pricing-box {
      border-color:$dark-border;
      .pricing-body{
        background-color: $light-layout;
        ul {
          li{
            border-color: $dark-border;
            a{
              color: $white-1;
            }
          }
        }
      }
    }
  }
  .pricing-table3 {
    .pricing-box{
      background-color:$dark-layout;
      border-color: $dark-border;
       .pricing-body {
        h3{
          color: $white-1;
        }
        ul {
          li{
            border-color: $dark-border;
            &:nth-child(odd){
              background-color:$light-layout;
            }
            a{
              color: $white-1;
            }
          }
        }
       }
    }
  }
  .hot-deal {

    
    .hot-deal-heading {
      h3{
        color: $font-layout-1;
      }
    }
    .hot-deal-contain {
      h5{
        color: $font-layout-1;
      }
      .timer{
        span{
          background-color:$light-layout;
          color: $font-layout-1;
        }
      }
    }

    &.hotdeal-two {
      .hot-deal-contain {
        .timer {
          span{
            background-color: $dark-layout;
            .timer-cal{
              color: $font-layout-3;
            }
          }
        }
      }
    }


  }
  .hotdeal-second {
    .hotdeal-box{
      background-color: $light-layout;
      .hotdeal-contain{
      
        h3{
          color: $white;
        }
        .timer2 {
          span{
            background-color: $light-layout1;
          }
        }
      }
    }
  }
  

  // team //
  .team2 {
    .team-box {
      .team-detail{
        background-color:$light-layout;
        h5{
          color: $white-1;
        }
      }
    }
  }

   



  // collection-banner //
  .collection-banner {
    .collection-banner-main{
      &:after {
        background-color: rgba($dark-layout,0.2);
      }
    }
  }
  .collection-wrapper  {
    .product-slick {
      background-color: $light-layout;
    }
  }
  .product-wrapper-grid {
    &.list-view {
      .product  {
        .product-box {
          background-color: $light-layout;
        }
      }
    }
  }
  .Jewellery-banner {
    h6 {
      color: $light-layout;
    }
    a {
      color: $light-layout;
    }
  }

  // blog //
  .blog {
    .blog-contain {
      .blog-details {
        background-color: $light-layout;
        p {
          color: $font-layout-5;
        }
      }
      h4 {
        color: $font-layout-3;
      }
      .blog-details-2 {
        .btn-normal{
          background-color: $light-layout;
          color: $white !important;
        }
      }

      &.blog-border{
        border-color: $dark-border;
        .blog-details-2{
          background-color: $dark-layout;
        }
      }
    }
  }
  .blog-detail-page  {
    .comment-section {
      li {
        border-color: $dark-border;
      }
    }
  }

  // breadcrumb //
  .breadcrumb-main {
    background-color: $light-layout;
    .breadcrumb-contain {
      h2 {
        color: $font-layout-1;
      }
      ul {
        li {
          i {
            color: $font-layout-3;
          }
          a {
            color: $font-layout-5;
          }
        }
      }
    }
  }

  // category //
  .box-category {
    .box-category-contain {
      background-color: $light-layout;
      h4 {
        color: $font-layout-3;
      }
      &:hover {
        background: $gray ;
      }
    }
  }
  

  .category2 {
    .category-box {
      background-color: $light-layout;
      .category-details {
        h6{
          color: $font-layout-1;
        }
      }
    }
  }

  .category3 {
    .category-box {
      .category-detail{
        background-color:$light-layout;
      }
    }
  }
  .category4 {
    .category-box{
      background-color: $light-layout;
      border-color: $dark-border;
      .category-detail{
        h2{
          color:$white-1;
        }
        ul{
          li{
            a{
              color: $white-2;
            }
          }
        }
      }
    }
  }
  .category5 {
    .category-box {
      .contain-wrraper{
        background-color: $light-layout;
        h3{
          color: $white-1
        }
        p{
          color: $white-2;
        }
      }
    }
  }


  // subscribe //
  .subscribe1 {
    .subscribe-contain {
      .subscribe-right {
        .input-group {
          .form-control{
            &:focus{
              background-color:$light-layout;
            }
          }
        }
      }
    }
  }
  .contact-banner2 {
    .subscribe-box{
      background-color:$light-layout;
      border-color: $dark-border;
      .form-control{
        &:focus{
          background-color:$dark-layout;
        }
      }
    }
    .contact-bannerbox{
      background-color:$light-layout;
      border-color: $dark-border;
      ul{
        li{
          svg{
            fill: $white;
          }
          h3{
            color: $white-2;
          }
        }
      }
    }
  }
  .brand-third{
    background-color: $light-layout;
    border-color: $dark-border;
    .brand-box{
      filter: invert(1);
    }
  }

  // element services//
  .services1 {
    .services-box{
      background-color: $light-layout;    
    }
    &.style2 {
      .services-box {
        .media {
          .media-body {
            h4{
              color: $white-1;
            }
            p{
              color: $white-2;
            }
          }
        }
      }
    }
    &.border-services {
      .services-box{
        border-color: $dark-border;
        background-color:$dark-layout;
        .media{
          .icon-wrraper{
            background-color:$dark-layout;
          }
        }
      }
    }
    &.services-transparent {
      .services-box{
        background-color: transparent;
      }
    }

  }
  .services2 {
    .services-box{
      background-color: $light-layout;
      border-color: $dark-border;
    }
  }  
  // media banner //
  .media-banner {
    background-color: $dark-layout;
    border-color: $dark-border;
    .media-banner-box {
      .media-heading {
        h5 {
          color: $font-layout-4;
        }
      }
      .media  {
        .media-body  {
          .media-contant  {
            p {
              color: $font-layout-2;
            }
            h6 {
              span{
                color: $font-layout-4;
              }
            }
            .cart-info {
              button {
                i{
                  color: $font-layout-1;
                }
              }
              a{
                i{
                  color: $font-layout-1;
                }
              }
            }
          }
        }
      }
    }
  }



  // discount-banner //
  .discount-banner {
    background-color: $light-layout;
    .discount-banner-contain {
      h1 {
        color: $font-layout-3;
      }
    }
  }

  // hote deal //
  .hot-deal {
    .hot-deal-contain1 {
      background-color: $light-layout;
      .hot-deal-heading {
        h5 {
          color: $font-layout-4;
        }
      }
      &.hot-deal-banner-1 {
        background-color: $dark-layout;
      }
      .hot-deal-subcontain {
        .hot-deal-center {
          h5 {
            color: $font-layout-5;
          }
          .timer {
            span {
              color: $font-layout-5;
            }
          }
        }
      }
    }
    .hot-deal-contain {
      background-color: $dark-layout;
      .hot-deal-center {
        .price {
          span {
            color: $font-layout-4;
          }
        }
        h5 {
          color: $font-layout-4;
        }
        .timer {
          span {
            background-color: $light-layout;
            color: $font-layout-5;
          }
        }
      }
      &.hot-deal-contain2 {
        .hot-deal-center {
          .timer {
            span {
              background-color: $dark-layout;
            }
          }
        }
