          color: $font-layout-5;
        }
      }
    }
  }
  .search-overlay {
    background-color: $light-layout;
  }

  // theme modal //

  
  .theme-modal {
    &#exampleModal {
      .offer-content {
        background-color: $light-layout;
      }
    }
    .modal-dialog {
      .modal-content {
        .modal-body {
          .modal-bg {
            .offer-content {
              h2 {
                color: $font-layout-2;
              }
              p {
                color: $font-layout-5;
              }
            }
          }
        }
      }
    }
  }
  .theme-modal {
    &#exampleModal  {
      .offer-content {
        p {
          color: $font-layout-5;
        }
      }
      .close {
        span {
          color: $font-layout-3;
        }
      }
    }
    
    .modal-dialog {
      .modal-content {
        .modal-body{
          background-color:$light-layout;
        }
      }
      .demo_modal ,.quick-view-modal {
        button{
          &.close{
            span{
              color: $font-layout-1;
            }
          }
        }
      }      
    }
  }

  

  // typografy //


  // review //
  .typography_section  {
    .typography-box  {
      .typo-content {
        .sub-title {
          color: $font-layout-3;
        }
        &.heading_content {
          h1,h2,h3,h4,h5,h6 {
            color: $font-layout-4;
          }
        }
      }
    }
  }

  // compare page //
  .compare-page  {
    .table-wrapper {
      .table {
        border-color: $dark-border;
        thead  {

          .th-compare {
            td {
              background-color: $light-layout;
              color: $font-layout-4;
              border-color: $dark-border;
            }
          }
          tr {
            border-color: $dark-border;
            td,th {
              background-color: $light-layout;
              color: $font-layout-4;
              border-color: $dark-border;
            }
            th {
              .remove-compare {
                color: $font-layout-4;
              }
            }
          }
        }
        tbody  {
          tr  {
            border-color: $dark-border;
            th,td {
              border-color: $dark-border;
              background-color: $light-layout;
              color: $font-layout-4;
            }
            p {
              color:$font-layout-5 ;
            }
          }
        }

      }
    }
  }
  .compare-section {
    .compare-part {
      .close-btn {
        color: $font-layout-3;
      }
      .detail-part  {
        .title-detail {
          background-color: $light-layout;
          border-color: $dark-border;
          h5 {
            color: $font-layout-3;
          }
        }
      }
      .img-secton {
        a {
          h5 {
            color: $font-layout-2;
          }
        }
      }
      .btn-part {
        border-color: $dark-border;
      }
    }
  }






  // collection //
  .collection  {
    .collection-block  {
      .collection-content  {
        h4 {
          color: $font-layout-3;
        }
        h3 {
          color:  $font-layout-2;
        }
      }
    }
  }


  // blog page //
  .blog-page  {
    .blog-sidebar  {
      .theme-card {
        background-color: $light-layout;
      }
    }
    .blog-media {
      background-color: $light-layout;
    }
  }



  
@media (max-width:1199px){
  .sm-horizontal,.sm-vertical {
    background-color: $light-layout;
    border-color: $dark-border;
    .mobile-back {
      color: $font-layout-3;
      border-bottom-color: $dark-border;
    }
  }
  // header 
  .category-header-4{
    background-color:$light-layout;
    .navbar-menu {
      .category-right {
        .icon-block {
          ul {
            li {
              svg{
                fill: $dark-link;
              }
            }
          }
        }
      }
    }
  }

}

@media (max-width:1470px){
  // header //
  .header-search {
    .input-group {
      .input-group-append {
        .input-group-text {
          select{
            padding-left: 30px;
            padding-right: 15px;
          }
        }
      }
    }
  }


}








}